.home {
  justify-content: center;
  text-align: center;
  flex-direction: column;
  width: 1920px;
  height: 1080px;
}

.title{
  width: 100%;
  height: 100%;
  margin: 0 auto;
  text-align: center;
  display : flex;
  justify-content : center;
  align-items : center;
}
.title > h1{
  width: 100%;
  font-family: Roboto;
  font-weight: bold;
  line-height: 1.31;
  color: #000000;
  font-size: 80px;
  white-space: nowrap;
}
.menu{
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background-color: #fff;
  display: flex;
  justify-content: center;
  text-align: center;
}
.menu > h3{
  margin: 120px 3px 0 0;
  font-family: Roboto;
  font-size: 40px;
  line-height: 1.33;
  letter-spacing: normal;
  color: #555555;
}
.menu > h3 > p {
  font-size: 60px;
  color: #000000;
}
@media only screen and (max-width: 1600px) {
  .title{
    width: 100%;
    padding: 62px 0px 58px 0px;
  }
 .title>h1{
  width: 100%;

 }
 .menu {
  width: 100%;
 }

 .menu>h3 {
width: 100%;

 }
 .menu>h3>p {
  width: 100%;

   }
}